@use "../_var.scss" as var;
@use "../_mixins.scss" as mix;

.projects{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include mix.sectionPadding("no");
    background: linear-gradient(var.$dark, var.$black);
    color: var.$white;
    h2{
        @include mix.title2('no');
        margin-bottom: 50px
    }
    .titleProjects{
        @include mix.titleProjects('no');
        cursor: pointer;
        transition: all 400ms ease-in;
        &:hover{
            color: var.$light;
            transform: translateY(5px);
        }
    }
    &-container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    &-project{
        margin: 20px 0;
        text-align: center;
        max-width: 800px;
        &-minia{
            width: 550px;
            margin: 20px 0;
            margin-top: 40px;
        }
        &-stack{
            &-container{
                display: flex;
                justify-content: center;
                align-items: flex-end;
                gap: 20px;
                margin: 20px 0;
            }
            img{
                width: 50px;
            }
        }
        &-more-false{
            max-height: 500px;
            transform: scaleY(1);
            transition: max-height 400ms ease-in,
                        transform 600ms ease-out;
        }
        &-more-true{
            max-height: 0;
            overflow: hidden;
            transform: scaleY(0);
            transition: max-height 400ms ease-in,
                        transform 800ms ease-out;
        }
    }
    @media screen and (max-width:768px){
        @include mix.sectionPadding("mobile");
        h2{
            @include mix.title2("mobile");
            margin-bottom: 25px;
        }
        .titleProjects{
            @include mix.titleProjects('mobile');
        }
        &-project{
            width: 100vw;
            &-minia{
                width: 275px;
            }
            &-stack{
                img{
                    width: 25px;
                }
            }
        }
    }
}